* {
     padding: 0;
     margin: 0;
     list-style: none;
     text-decoration: none;
     box-sizing: border-box;
     font-family: 'Louis George Cafe';
     -webkit-overflow-scrolling: touch;

     &:focus {
        outline: 0;
     }
}

h6 {
   font-size: 16px;
   color: #fff;
   font-weight: normal;
   padding-bottom: 25px;
   display: flex;
   justify-content: space-between;
   align-items: center;
}

.page {
   padding: 24px;
}
